
body {
  margin: 0;
  padding: 0;
  background: #1e1e23;
  color: #f5f5f5;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 60px;
}

#home {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.button {
  display: block;
  max-width: 200px;
  padding: 8px 15px;
  margin-bottom: 15px;
  font-size: 22px;
  color: #f5f5f5;
  border: 1px solid #f5f5f5;
  border-radius: 22px;
  text-decoration: none;
  -webkit-transition: color .3s, border-color .3s;
  transition: color .3s, border-color .3s;
}
.button:hover {
  color: #EDC2AF;
  border-color: #EDC2AF;
}

header {
  margin-bottom: 30px;
}
header a {
  font-size: 48px;
  font-weight: 700;
  color: #f5f5f5;
  text-decoration: none;
  letter-spacing: 0.09em;
  -webkit-transition: letter-spacing .3s;
  transition: letter-spacing .3s;
}
header a:hover {
  letter-spacing: 0.18em;
}

.button--login {
  color: #59ef65;
  border-color: #59ef65;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.filter-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.filter-list li {
  padding: 3px 6px;
  margin: 0 5px 10px;
  border: 1px solid #f5f5f5;
  border-radius: 12px;
}

